
@import "./../Main/helpers/variables";

.header {
  position: fixed;
  top: 0;
  width: 100%;
  min-height: 100px;
  z-index: 5;


  @media (orientation: landscape) and (max-width: 840px) {
    width: calc(100vw - 72px);
  }

  @media (orientation: landscape) and (max-width: 860px) and (max-height: 340px) {
    width: calc(100vw - 64px);
  }


  @media (orientation: landscape) and (max-width: 620px) {
    width: calc(100vw - 64px);
  }
  @media (orientation: landscape) and (max-height: 370px) and (max-width: 640px) {
    width: calc(100vw - 64px);
  }

  //
  //&:after{
  //  content: "";
  //  position: fixed;
  //  top: 0;
  //  left: 50%;
  //  width: 1px;
  //  height: 100vh;
  //  background-color: #f00;
  //  z-index: 10000;
  //}

}

.header.header-registred{
  min-height: 55px;
}



.header_controls {
  width: 100%;
  height: 55px;
}


.header_controls .container{
  width: calc(100% - 20px);
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}


.header_title{
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
  top: calc(50% - 12px);
  font-size: 14px;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.35);




  @media (min-width:350px) {
    top: calc(50% - 12px);
    font-size: 16px;
  }

  @media (max-width:390px) and (max-height:380px)  {
    top: calc(50% - 12px);
    font-size: 12px;
  }

  @media (min-width:768px) {
    top: calc(50% - 15px);
    font-size: 20px;
  }

}



.header_userControls{
  background: url("images/bg/header-bg.jpg") 50% 50% no-repeat;
  background-size: cover;
}
///////////////

  .btn_returnFromCorrecting{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
  }

////////////////

.user_control{
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  //min-width: 195px; /// Compensation
}


.user_control__img{
  object-fit: cover;
  margin-right: 8px;
}


.user_control__name{
  position: relative;
  color: #fff;
  font-weight: 500;
  font-size: 15px;
  width: 100%;
  max-width: calc(100vw - 190px);
  text-align: center;
  white-space: nowrap;
  text-overflow: ellipsis;
  padding-right: 5px;
  overflow: hidden;

  text-shadow: 0 0 3px rgba(0, 0, 0, 0.28);
}


///

.header_userInfo{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

}

.coin_block{

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.coin_img{
  position: relative;
  filter: drop-shadow(1px -2px 14px #FF5656), drop-shadow(-3px 2px 4px rgba(0, 0, 0, 0.25));
  z-index: 2;
}


.coin_value{
  position: relative;
  padding-left: 10px;
  padding-right: 20px;
  min-width: 50px;
  display: inline-block;


  font-weight: 600;
  font-size: 12px;
  color: #FFD600;
  z-index: 1;
  &:before{
    content: '';
    position: absolute;
    top: -2px;
    left: -10px;
    width: 100%;
    height: 20px;
    border-radius: 72px;
    background: #000;
    opacity: 0.3;
    z-index: -1;
  }

  @media (min-width:860px) {
    min-width: 100px;
  }


}



.btn_navigation{
  width: 40px;
  height: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
}


