.header_authorization{
  background-color: #261B44;
}

.btn_enter {
  width: calc(100% - 236px);
  margin-left: 0;
  filter: drop-shadow(0px 8px 13px rgba(119, 21, 202, 0.33));
  background: $btn-purple;
  &:after{
    content: "";
    position: absolute;
    left: 3px;
    top: 2px;
    width: calc(100% - 6px);
    height: 40px;
    z-index: 1;
    background: #261B44;
    border-radius: 10px;
  }
  @media (min-width:390px) {
    width: 100%;
  }

  @media (min-width:500px) {
    width: auto;
  }

}

.btn_enter > span{
  position: relative;
  z-index: 2;
}

.btn_registration {
  margin-right: 0;
  width: 100%;
  text-align: center;
  line-height: 44px;
  padding: 0 10px;
  max-width: 216px;
  background: $btn-purple;
  box-shadow: 0 8px 13px rgba(119, 21, 202, .33);
}




