@import "./../../Main/helpers/variables";

.popup_nextLevel{
  position: relative;
  padding: 30px 26px;
  background: linear-gradient(rgb(51, 18, 66), rgb(51, 18, 66)),
  linear-gradient(90deg, #5A089A 0%, #DDB1FF 50%, #6A1CA7);
  background-origin: padding-box, border-box;
  background-repeat: no-repeat;
  border: 1px solid transparent;
  border-radius: 8px;
  text-align: center;
}

.popup_nextLevel__rankImg{
  position: absolute;
  top: -33px;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  width: 66px;
  height: 66px;
}

.popup_nextLevel__сongratulations{
  margin: 40px auto 10px;
}

.popup_nextLevel__text{
  font-size: 14px;
  margin: 10px auto;
}

.popup_nextLevel__button{
  margin: 40px auto 20px;
  width: 100%;
  max-width: 220px;
  height: 44px;
  line-height: 44px;
  background:  $btn-purple;
  box-shadow: 0 8px 13px rgba(119, 21, 202, 0.33);
  border-radius: 10px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  @media (min-width:360px) {
    max-width: 257px;
  }


  &:hover{
    background: $btn-color-hover;
  }

}


.popup_nextLevel__coins{
  display: inline-block;
  margin-right: 4px;
  filter: drop-shadow(1px -2px 6px #FF5656);
}