@import "./../../Main/helpers/variables";


.changePass{


  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: calc(100vh - 150px);
  width: 100%;

  @media (max-height: 450px) and (min-width: 320px) {
    margin-top: 85px;
  }

}


.changePass_form{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  max-width: 600px;
}

.changePass_form .label_element{
  max-width: 450px;
}


.btn_changePass{
  margin: 30px auto 0;
  width: 100%;
  max-width: 340px;
  background: $btn-purple;

  @media (min-width:450px) {
    margin-top: 18px;
  }


  &:hover{
    background: $btn-color-hover;
  }
}