body {
  position: relative;
  font-family: "Fira Sans", sans-serif;
  font-size: 15px;
  line-height: 1.4;
  font-weight: 400;
  min-width: 320px;
  display: -webkit-box;
  display: flex;
  -webkit-box-orient: vertical;
  flex-direction: column;
  color: #fff;
  min-height: 100vh;
  background: #0c0c0c;
  background-blend-mode: overlay;
}


.noScroll {
  height: 100vh;
  overflow: hidden;
}


.FocusOnInput{
  .menu{
    display: none;
  }
  .main_content{
    width: 100vw!important;
  }
  .header{
    display: none;
  }
}



img {
  max-width: 100%;
  height: auto;
}

figure {
  margin: 0;
}

a {
  text-decoration: none;
  color: #fff;
}

a:hover,
a:focus {
  text-decoration: none;
  color: #fff;
}


p {
  margin: 0;
  padding: 0;
}

ul, ol {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

button {
  border: none;
  padding: 0;
  margin: 0;
  cursor: pointer;
  background: transparent;
  outline: none;
}


input:focus::placeholder,
textarea:focus::placeholder {
  opacity: 0;
}

input::placeholder,
textarea::placeholder {
  opacity: 1;
  transition: opacity .3s;
}

input::placeholder::-moz-focus-outer,
textarea::placeholder::-moz-focus-outer,
input::placeholder::-moz-focus-inner,
textarea::placeholder::-moz-focus-inner {
  border: 0;
  outline: none;
}


details summary::-webkit-details-marker {
  display: none
}

details > summary {
  position: relative;
  list-style: none;
}

/* Добавляем собственный маркер для закрытого состояния */
details summary:before {
  content: '';
  position: absolute;
  top: 12px;
  right: 5px;
  width: 20px;
  height: 20px;
  background: url("../App/Components/images/arrow_down.svg") 50% 50% no-repeat;
  transition: .2s ease-in;
  transform: rotate(0deg);
  z-index: 2;
}

/* Добавляем собственный маркер для открытого состояния */
details[open] summary:before {
  transform: rotate(180deg);
}



.info_details{
  box-sizing: border-box;
  border-bottom: 1px solid rgba(255, 255, 255, .2);
  &:first-child{
    border-top: 1px solid rgba(255, 255, 255, .2);
  }
}

.info_details[open] .info_val {
  animation: slide .3s ease-in-out;
}

@keyframes slide {
  0%    {opacity: 0; transform:  translate(0, -10px); }
  100%  {opacity: 1; transform:  translate(0, 0);}
}


.info_details summary{
  outline: none;
  font-weight: 500;
  font-size: 16px;
  min-height: 26px;
  line-height: 26px;
  padding: 10px 20px 10px 0;
  cursor: pointer;
}

.info_details > div{
  color: #fff;
  padding-bottom: 20px;
}
.info_details > div p{
  margin-bottom: 10px;
}



.container {
  display: block;
  margin: 0 auto;
  width: 96%;
  box-sizing: border-box;
  //border-left: 1px solid #fff;
  //border-right: 1px solid #fff;
  max-width: 1620px;

  @media (max-width: 1800px) {
    max-width: 1420px;
  }

  @media (max-width: 1520px) {
    max-width: 1260px;
  }

  @media (max-width: 1490px) {
    max-width: 1120px;
  }

  @media (max-width: 1280px) {
    max-width: 970px;
  }
  @media (max-width: 1120px) {
    max-width: 850px;
  }

  @media (max-width: 991px) {
    max-width: 780px;
  }

  @media (max-width: 900px) {
    max-width: 720px;
  }

  @media (max-width: 768px) {
    max-width: 600px;
  }
  @media (max-width: 490px) {
    max-width: initial;
    width: calc(100% - 20px);
    padding: 0;
  }
}

