h1 {
  color: #fff;
  font-weight: bold;
  font-size: 26px;
  line-height: 31px;
}

.hidden {
  display: none !important;
}


.btn {
  position: relative;
  margin: 5px 10px;
  min-width: 114px;
  font-weight: bold;
  font-size: 14px;
  text-transform: uppercase;
  color: #fff;
  height: 44px;
  border-radius: 10px;
}


.wrapperBlock {

  display: -webkit-box;
  display: flex;

  -webkit-box-orient: vertical;
  flex-direction: column;


  width: 100%;
  max-width: 100vw;
  height: 100%;
  min-height: 100vh;
  justify-content: space-between;

}


.module_checkbox{
  align-self: flex-start;
  position: relative;
  margin-top: 24px;
  margin-bottom: 12px;
}

.module_checkbox__input{
  position: absolute;
  top: 0;
  left: 0;
  width: 18px;
  height: 18px;
  //z-index: 5;
}

.module_checkbox__text{
  position: relative;
  display: inline-block;
  padding-left: 25px;
  font-size: 14px;
  color: #D1D0E5;
  cursor: pointer;
}
.module_checkbox__text:before{
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 18px;
  height: 18px;
  border-radius: 4px;
  background: #C5B7FF;
}

.module_checkbox__input:checked + .module_checkbox__text:after{
  content: "";
  position: absolute;
  top: 4px;
  left: 2px;
  background: url("./../App/Components/images/icon-checkbox.svg") 50% 50% no-repeat;
  width:14px;
  height:12px;
  z-index: 3;
}

.label_element{
  position: relative;
  width: 100%;
}




.input_text{
  background: none;
  margin: 18px 5px;
  padding: 6px 0;
  width: 100%;
  border:0;
  border-bottom: 1px solid rgba(255, 255, 255, .3);
  border-radius: 3px;
  line-height: 20px;
  color: #D1D0E5;
  font-size: 16px;
  outline: none;

  &::placeholder{
    color: inherit;
  }
}





.text_error{
  position: absolute;
  top: calc(100% - 10px);
  left: 5px;
  font-size: 14px;
  font-weight: 500;
  color:#f00;
  @media (min-width:450px) {
    font-size: 16px;
  }
}


::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-thumb {
  border-width: 1px 1px 1px 2px;
  border-color: #777;
  background-color: #4F15CA;
}


::-webkit-scrollbar-track-piece {
  background-color: #181A25;
}



