@import "./../helpers/variables";


.profile{
  position: relative;
  margin: 12px auto 0; /// -33px
  max-width: 500px;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  z-index: 3;
}

.profile_top__img{
  position: relative;

  z-index: 5;
}


.profile_top{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.profile_top__name{
  margin-top: 14px;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  text-shadow: 0 0 3px rgba(0, 0, 0, 0.28);
}


.profile_top__rank{
  margin-top: 15px;

  font-weight: bold;
  font-size: 12px;
  text-transform: uppercase;
  color: #D1D0E5;
  text-shadow: 0 0 3px rgba(0, 0, 0,.28);
}




.profile_middle{
  margin-top: 40px;
  width: 84%; ////
}


.profile_rank{
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}



.profile_rank__btn{
  position: relative;
  z-index: 5;
}

.profile_rank__other{

  width: 34px;
  height: 34px;
  z-index: 2;

  @media (min-width:450px) {
    width: 45px;
    height: 45px;
  }
}



.profile_coins{
  margin: 30px auto 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  text-align: center;
}

.profile_coins__img{
  display: block;
  width: 49px;
  height: 49px;

  //filter: drop-shadow(1px -2px 24px #FF5656), drop-shadow(-3px 2px 4px rgba(0, 0, 0, 0.25));
  filter: drop-shadow(1px -2px 14px #FF5656)
}


.profile_coins__line{
  position: relative;
  margin-top: 15px;
  width: 100%;
  max-width: 500px;
  font-weight: 500;
  font-size: 16px;
  padding: 3px 0;
  color: $yellow;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 72px;
}


.profile_coins__link{
  position: absolute;
  top: 3px;
  right: 3px;
  width: 22px;
  height: 22px;
  border-radius: 50%;
  background: $green;
  &:before,
  &:after{
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transform-origin: 50% 50%;
    width: 12px;
    height: 2px;
    background-color: #fff;
    z-index: 2;
  }
  &:after{
    transform: translate(-50%, -50%) rotate(90deg);
  }
}



//////////////

.profile_bottom{
  width: 100%;
  max-width: 500px;
  margin: 34px auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.profile_form__full{
  width: calc(100% - 64px);
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;


  position: relative;
  min-height: 120px;
  padding: 32px 30px;

  background: linear-gradient(rgb(51, 18, 66), rgb(51, 18, 66)),
  linear-gradient(90deg, #5A089A 0%, #DDB1FF 50%, #6A1CA7);
  background-origin: padding-box, border-box;
  background-repeat: no-repeat;
  border: 1px solid transparent;


  border-radius: 8px;

  @media (min-width:450px) {
    font-size: 14px;
  }


}


.profile_form__top{
  width: 100%;
  margin-bottom: 30px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

.profile_form__block{
  margin-left: 13px;
  font-size: 12px;
  @media (min-width:450px) {
    font-size: 14px;
  }
}


.profile_form__text{
  color: #AE92A4;
  text-shadow: 0 0 3px rgba(0, 0, 0, 0.28);
}


////

.profile_form{
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 32px;
  color: #fff;
}

.profile_form .label_element{
  width: calc(100% - 40px);
}


.profile_form__input{
  box-sizing: border-box;
  width: 100%;
  height: 30px;
  color: #fff;
  line-height: 30px;
  background: none;
  border:0;
  //border:1px solid #f00;
  &::placeholder{
    color: #fff;
  }
}
.profile_form .text_error{
  top: 100%;
}

.profile_form__change{
  width: 30px;
  height: 30px;
  //border: 1px solid #f00;
}


.profile_form__change img{
  object-fit: contain;
}



.link_changePass{

  width: 100%;
  max-width: 300px;
  height: 44px;
  line-height: 44px;
  font-weight: bold;
  margin: 40px auto 0;
  text-align: center;
  text-transform: uppercase;
  background: $btn-purple;
  box-shadow: 0 8px 13px rgba(119, 21, 202, 0.33);

  &:hover{
    background: $btn-color-hover;
  }
}