///// Popup INFO

.popup_info{
  padding: 32px 30px;
  background: linear-gradient(rgb(51, 18, 66), rgb(51, 18, 66)),
  linear-gradient(90deg, #5A089A 0%, #DDB1FF 50%, #6A1CA7);
  background-origin: padding-box, border-box;
  background-repeat: no-repeat;
  border: 1px solid transparent;
  border-radius: 8px;
}
.popup_info__inner {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.item_popup {
  position: relative;
  text-align: left;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  &:not(:last-child) {
    margin-bottom: 26px;
  }
}

.item_popup:hover .item_popup_name{
  text-decoration: underline;
}

.item_popup__img{
  margin-right: 10px;
}
