.menu {
  position: fixed;
  right: 0;
  bottom: 0;
  z-index: 10;
  width: 100%;





  @media (orientation: landscape) and (min-height: 280px) {
    width: initial;
  }

  @media  (max-width: 450px) and (min-height: 400px) {
    width: 100%;
  }

  @media  (min-height: 600px) and (max-width:600px) {
    width: 100%;
  }

  @media (orientation: landscape) and (max-width: 570px){
    width: initial;
  }

}

.menu_list {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  height: 55px;
  background: linear-gradient(180deg, rgba(78, 21, 49, .9) 0%, rgba(1, 0, 65, .9) 100%),
  linear-gradient(0deg, rgba(0, 0, 0, .25), rgba(0, 0, 0, .25)),
  linear-gradient(180deg, #7032AE 0%, #4B32AE 100%);
  box-shadow: 0px -4px 10px rgba(0, 0, 0, 0.45);
  backdrop-filter: blur(20px);


  //@media (orientation: landscape) and (min-height: 140px) {  /// For field
  //  height: 100vh;
  //  width: 64px;
  //  flex-direction: column;
  //}


  @media (orientation: landscape) and (min-height: 280px) {  /// IphoneS
    height: 100vh;
    width: 64px;
    flex-direction: column;
  }

  @media (orientation: landscape) and (min-height: 370px) {
    width: 72px;
  }


  @media (min-width: 860px) {
    height: 100vh;
    width: 72px;
    flex-direction: column;
  }

}

.menu_item {
  position: relative;
  min-width: 64px;
  height: 55px;
  padding-bottom: 2px;

  @media (min-width: 370px) {
    min-width: 72px;
  }

  @media (orientation: landscape) and (min-height: 300px) {
    margin: 10px 0;
    height: 50px;
  }





  @media (min-width: 860px) {
    margin: 20px 0;
  }

  @media (min-width: 800px) and (max-height: 450px) {
    margin: 10px 0;
  }
  @media (min-width: 800px) and (max-height: 360px) {
    margin: 5px 0;
  }
  @media (orientation: landscape) and (max-width: 570px) {  /// Iphone 5 horizontal
    min-width: 60px;
    margin: 0 0 12px;
  }


}

.menu_link {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

}

.menu_img{
  width: 38px;
  height: 38px;
  background-image: url("./../Components/images/menu/css_sprites.png");
  background-repeat: no-repeat;
}

.menu_link__work .menu_img{
  background-position:  -304px 0;
}

.menu_link__bonuses .menu_img{
  background-position:  0 0;
}


.menu_link__main .menu_img{
  background-position: -76px 0;
}

.menu_link__tournament .menu_img{
  background-position:-228px 0;
}

.menu_link__rating .menu_img{
  background-position: -152px 0;
}



.active{
  &.menu_link__work .menu_img{
    background-position:-342px -0;
  }

  &.menu_link__bonuses .menu_img{
    background-position: -38px -0;
  }

  &.menu_link__main .menu_img{
    background-position: -114px -0;
  }

  &.menu_link__tournament .menu_img{
    background-position:-266px -0;
  }

  &.menu_link__rating .menu_img{
    background-position:-190px -0;
  }
}


.menu_link.active {
  position: absolute;
  top: -6px;
  left: 0;
  padding-top: 6px;
  width: 100%;
  text-align: center;
  background: linear-gradient(180deg, #49154E 0%, #3D0080 100%);
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.28);
  border-radius: 4px;

  .menu_name {
    color: #fff;
    font-weight: bold;
  }

  @media (orientation: landscape) and (max-width: 570px) {  /// Iphone 5 gorizontal
    width: 72px;
    top: 0;
    left: -6px;
  }
  @media (orientation: landscape) and (min-height: 350px) {
    width: 72px;
    left: -6px;
  }
  @media (orientation: landscape) and (min-height: 350px) and (min-width: 600px) {
  left: -5px;
  padding-left: 3px;
  }

  @media (max-height: 360px) {
    top: 0;
  }


}

.menu_name {
  color: #D1D0E5;
  font-size: 11px;



  @media (min-width: 360px) {
    font-size: 14px;
  }
  @media (max-height: 360px) {
    font-size: 11px;
  }

}

