@import "./../helpers/variables";
.single_tournament{
  margin: 30px auto;
  width: 100%;
}

.single_tournament .tournament_item{
  margin: 0 auto;
  max-width: 650px;
}



.btn_enterIntoTournament,
.btn_leftTournament{
  display: block;
  margin: 30px auto;
  width: 270px;
  background: $btn;
  border-radius: 10px;
  font-weight: bold;
  font-size: 18px;
  line-height: 22px;
  &:hover,
  &:focus{
    background:  $btn-color-hover;
  }
}

.btn_leftTournament{
  background: $btn-red;
}


.bg-block{
  padding: 23px 27px 36px;
  background: linear-gradient(
                  180deg, rgba(78, 21, 49, 0.9) 0%, rgba(1, 0, 65, 0.9) 100%), linear-gradient(
                  90deg, #5A089A 0%, #DDB1FF 50%, #6A1CA7);
  background-origin: padding-box, border-box;
  background-repeat: no-repeat;
  border: 1px solid transparent;
  border-radius: 16px;
  text-align: center;
}

.tournament_rules__text{
  text-align: center;
  font-weight: bold;
  font-size: 16px;
  line-height: 20px;
}

.tournament_rules__list{
  margin: 15px auto;
}

.tournament_rules__list li{
  margin-bottom: 15px;
}



//////////// TABLE T

.table_t{
  width: 100%;
  max-width: 600px;
  margin: 30px auto;
  text-align: center;
}

.table_t__title{
  padding: 10px 0;
  background: #3B2D62;
  font-weight: 500;
  font-size: 18px;
  color: #fff;
}

.table_t__item{
  padding: 0 10px;
  height: 50px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  &:nth-of-type(even){
    background: rgba(255, 255, 255, 0.05);
  }
}

.table_t__item.user{
  background: linear-gradient(92deg, #FFFFFF -98%, #AFE2FF 100%);
  box-shadow: 0 0 7px rgba(213, 247, 255, 0.6),
  inset 0 0 10px rgba(255, 254, 254, 0.61);


  .table_t__number{
    color: #66656F;
  }
  .table_t__nick{
    color: #120C23;
  }
  .table_t__id{
    color: #66656F;
  }
  .table_t__coins{
    color: #3B2D62;
  }
}


.table_t__left{
  max-width: calc(100% - 70px);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.table_t__number{
  min-width: 30px;
  font-weight: 900;
  font-size: 14px;
  color: #D1D0E5;
  letter-spacing: -0.04em;
  &:before{
    content: "#";
    color: inherit;
    margin-right: 3px;
  }
}

.table_t__img{
  width: 40px;
  height: 40px;
  margin-right: 9px;
}

.table_t__nick{
  text-align: left;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  color: #fff;


}

.table_t__id{
  text-align: left;
  font-size: 14px;
  color: #D1D0E5;
}


.table_t__right{
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
}

.table_t__coin{
  width: 25px;
  height: 25px;
  margin-right: 8px;
}

.table_t__coins{
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;


  background: linear-gradient(to left, #FFC700 0%, #FFA800 50%, #FFD600 100%);
  background-size: 100%;
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}