.popup_ranks{
  padding: 25px 10px 10px 10px;
  width: 260px;

  max-height: 90vh;
  overflow-y: auto;

  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(12px);
  border-radius: 8px;


  @media (min-width:360px) {
    width: 280px;
  }
  @media (min-width:450px) {
    width: 310px;
  }
}





.popup_ranks__text{
  font-weight: bold;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
  margin-bottom: 18px;
}

.popup_ranks__list{
  position: relative;
  width: 100%;
  max-width: 310px;
}

.popup_ranks__item{
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  &:not(:last-child){
    margin-bottom: 16px;
  }
}
.popup_ranks__item.rank_checked{
  opacity: .5;
}



.popup_ranks__left,
.popup_ranks__right{
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}


.popup_ranks__img{
  width: 40px;
  height: 40px;
  margin-right: 8px;
}

.popup_ranks__name{
  font-weight: 500;
  font-size: 15px;
  line-height: 22px;

  @media (min-width:360px) {
    font-size: 18px;
  }

}

.popup_ranks__coin{
  display: block;
  margin-right: 10px;
  filter: drop-shadow(1px -2px 8px #FF5656);
}

.popup_ranks__done{
  text-transform: uppercase;
  font-weight: bold;
  font-size: 16px;
}