@import "./../../Main/helpers/variables";



.source{
  margin: 32px 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.source_chest{
  position: relative;
  display: inline-block;

  overflow: hidden;
  border-radius: 8px;
  margin: 0 auto 10px;
}



.source_chest__img{
  border-radius: 8px;
}

.source_chest__text{
  position: absolute;
  bottom: 7px;
  left: 1px;
  padding: 15px 0;
  width: calc(100% - 2px);
  z-index: 2;
  text-align: center;
  background: rgba(18, 12, 35, 0.5);
  backdrop-filter: blur(8px);
  line-height: 20px;
  border-radius: 8px;
  color: #fff;
}

.source_list{
  width: 100%;
}
.source_item{
  width: 100%;
  background: #3B2D62;
  border-radius: 8px;
  &:not(:first-child){
    margin-top: 10px;
  }
}

.source_item__button{
  position: relative;
  width: 100%;
  display: -webkit-flexbox;
  display: flex;

  -webkit-box-orient:horizontal;
  flex-direction: row;

  -webkit-box-pack: justify;
  justify-content: space-between;

  -webkit-box-align:center;
  align-items: center;
  box-sizing: border-box;
  padding: 15px 5px 15px 20px;
}


.source_item__text{
  display: inline-block;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  text-align: left;
}

.source_item__val{
  position: relative;
  margin-left: 2px;
  min-width: 105px;
  font-weight: bold;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;


  font-size: 20px;
  line-height: 24px;
  color: $yellow;
  text-align: right;
  &:before{
    content: url("./../../Components/images/coin.svg");
    position: relative;
    margin-right: 6px;
    width: 25px;
    height: 25px;
    filter: drop-shadow(1px -2px 8px #FF5656);
  }
}


.source_completed{
  display: block;
  margin: 33px auto;
  background: #2E293D;
  border-radius: 18px;
  width: 110px;
  height: 25px;
  text-align: center;
  font-size: 12px;
  color: #fff;
  line-height: 25px;

  @media (min-width:450px) {
    height: 40px;
    line-height: 40px;
    font-size: 16px;
  }
}

.source_item__button[disabled]:after,
.source_chest[disabled]:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(59, 45, 98, .5);
  z-index: 1;
}
