//////  POPUPS
@import "../Main/helpers/variables";

.ShadowBlock {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  background-color: rgba(22, 0, 50, .85);
  z-index: 15;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}


.popupInner {
  position: relative;
  min-height: 120px;
}


.ranksPopup{
  @media (max-height:680px) {
    justify-content: flex-start;
  }
}


.ranksPopup .popupInner{
  @media (max-height:680px) {
    margin-top: 40px;
    max-height: 70vh;
  }
}

.ranksPopup .popup_ranks__list{
  @media (max-height:680px) {
    max-height: 70vh;
    overflow-x: hidden;
    overflow-y: auto;
    padding-right: 5px;
  }
}


.nextLevelPopup .popupInner{
  @media (max-width:360px) {
    width: 280px;
  }
}



.btn_closePopups {
  position: absolute;
  right: -20px;
  top: -20px;
  width: 40px;
  height: 40px;

  &:before {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 24px;
    height: 24px;
    background-color: #fff;
    border-radius: 50%;
    z-index: 1;
  }
}

.btn_closePopups__icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;

}


