@import "./../../helpers/variables";


.prizeFund {




  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}


.prizeFund_title {
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
}

.prizeFund_sum {

  margin: 23px auto 0;
  font-weight: 900;
  font-size: 26px;
  background: linear-gradient(to left, #FFC700 0%, #FFA800 50%, #FFD600 100%);
  background-size: 100%;
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}

.prizeFund_text{
  margin-top: 23px;
  margin-bottom: 14px;
}

.prizeFund_list{
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;

  @media (min-width:390px) {
    justify-content: space-around;
  }

}


.prizeFund_item{
  width: 80px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;

  margin-bottom: 25px;

  @media (min-width:340px) {
    width: 85px;
  }


  @media (min-width:400px) {
    width: 104px;
  }

  @media (min-width:1120px) {
    width: auto;
  }
}


.prizeFund_number{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #3B2D62;

  width: 55px;
  height: 55px;
  border-radius: 50%;
  color: #fff;
  font-weight: 900;
  font-size: 26px;
}


.prizeFund_value{
  position: relative;
  margin-top: -8px;
  width: 64px;
  height: 20px;
  line-height: 20px;
  border-radius: 10px;
  background: #fff;
  color: #66656F;
  font-size: 12px;
  font-weight: bold;
  z-index: 2;

  @media (min-width:450px) {
    width: 80px;
    height: 20px;
    line-height: 20px;
    font-size: 15px;
  }

}

.prizeFund_item:nth-child(1) {
  .prizeFund_number{
    background: url("./../../../Components/images/tournaments/Prize-Fund/star-gold.svg");
    text-shadow: 0 1px 1px rgba(0, 0, 0, 0.35);
  }
}

.prizeFund_item:nth-child(2) {
  .prizeFund_number{
    background: url("./../../../Components/images/tournaments/Prize-Fund/star-silver.svg");
    text-shadow: 0 1px 1px rgba(0, 0, 0, 0.35);
    background-size: contain;
    color: #66656F;
  }
}

.prizeFund_item:nth-child(3) {
  .prizeFund_number {
    background: url("./../../../Components/images/tournaments/Prize-Fund/star-brons.svg");
    text-shadow: 0 1px 1px rgba(0, 0, 0, 0.35);
    background-size: contain;
  }
}



