.tournaments_list{
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 20px;

  @media (min-width:768px) {
    grid-template-columns: repeat(2, 1fr);
  }

  @media (min-width:1490px) {
    grid-template-columns: repeat(3, 1fr);
  }

}

.tournaments_list .tournament_item{
  margin-right: 0;
}