.game_frame{
  position: relative;
  width: 100vw;
  height: 100vh;
  overflow: hidden;


}


.game_frame__plug{
  position: absolute;
  top: 0;

  width: 100%;
  height: 100vh;
  background-color: #000;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 1000;


  @media (min-width:600px) {
    display: none;
  }
  @media (orientation: portrait) and (max-width:450px) {
    display: flex;
  }
  @media (orientation: landscape) {
    display: none;
  }

}


.plug_video{
  max-width: 100vw;
}


.btn_closeGame{
  position: absolute;
  top: 10px;
  right: 10px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #fff;
  z-index: 999;
}
.btn_closeGame__icon{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

}