.profile_rank__elem {
  position: relative;
  width: calc(100% - 68px);
  margin: 0 auto;
  text-align: center;
  @media (min-width: 450px) {
    width: calc(100% - 90px);
  }
}

.profile_rank__text {
  position: absolute;
  left: 50%;
  top: -34px;
  transform: translateX(-50%);
  font-size: 12px;
  color: #D1D0E5;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.35);


  @media (min-width: 450px) {
    font-size: 14px;
  }
}


@for $i from 0 through 100 {
  .progress progress[data-value="#{$i}"]
  {
    & ~ .progress-bg .progress-bar { width: $i * 1% }
  }
}

.progress
{position: relative;
  overflow: hidden;
  progress
  {
    position: absolute;
    width: 0;
    height: 0;
    overflow: hidden;
    left: -777px;
  }
}

.progress-value
{
  color: #333;
  display: block;
  line-height: 21px;
  text-align: center;
}

.progress-bg
{
  background: rgba(255, 255, 255, .4);
  box-shadow: 0 3px 7px rgba(0, 0, 0, 0.4);
  position: relative;
  height: 4px;
  border-radius: 8px;
  overflow: hidden;


  @media (min-width:450px) {
    font-size: 8px;
  }

}

@keyframes progress_bar {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: -40px 0;
  }
}

.progress-bar
{
  overflow: hidden;
  background: linear-gradient(91deg, #FFF -98%, #AFE2FF 99%);
  box-shadow: 0 0 7px #00B2FF, 0 0 6px rgba(0, 0, 0, 0.4);
  border-radius: 8px;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  transition: width 1s linear;
  &:after
  {

    animation: progress_bar 0.8s linear infinite;
    background-image: linear-gradient(45deg,rgba(255,255,255,.15) 25%,transparent 25%,transparent 50%,rgba(255,255,255,.15) 50%,rgba(255,255,255,.15) 75%,transparent 75%,transparent);
    background-size: 40px 40px;
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}



